<template>
  <div style="padding-top: 80px;border-bottom: 1px #b3b3b3 solid;padding-bottom: 10px" id="course-head">

    <span class="courseNamecss">{{ courseName }}</span>
    <router-link :to="{path:'/Evaluation'}">课程评价</router-link>
    <div style="float: right;margin-right: 100px;margin-top: -10px;">
      <el-menu default-active="/Section/Sectionindex" class="el-menu-demo" mode="horizontal" router>
        <el-menu-item index="/Section/Sectionindex">首页</el-menu-item>
        <el-menu-item index="/Section/Task">任务</el-menu-item>
        <el-menu-item index="/Section/Statistics">统计</el-menu-item>
        <el-menu-item index="/Section/Material">资料</el-menu-item>
        <el-menu-item index="/Section/Notify">通知</el-menu-item>
        <el-menu-item index="/Section/Operation">作业</el-menu-item>
        <el-menu-item index="/Section/Exam">考试</el-menu-item>
        <el-menu-item index="/Section/Discuss">讨论</el-menu-item>
      </el-menu>
    </div>
  </div>
</template>

<script>
export default {
  name: "CourseHead",
  data() {
    return {
      courseName: localStorage.getItem("courseName"),
      index: localStorage.getItem("courseIndex")
    }
  },
}
</script>

<style scoped>
.courseNamecss {
  font-size: 30px;
  margin-left: 300px;
  color: #7b9e31;
}

ul li {
  display: inline;
  margin-left: 20px;
  font-size: 20px;
}

a {
  margin-left: 10px;
  color: #7b9e31
}
</style>



